/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

#sidebar {
    width: 320px;
    position: fixed;
    top: 0;
    left: -320px;
    height: 100vh;
    z-index: 999;
    background: #ffffff;
    color: #000000;
    transition: all 0.3s;
    overflow-y: scroll;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2);
    height: 100vh; 
    overflow: hidden;
}

#sidebar.active {
    left: 0;
}

#dismiss {
    text-align: center;
    position: absolute;
    top: 10px;
    right: 10px;
    color: #ffffff;
    cursor: pointer;
    transition: all 0.3s;
}

.overlay {
    display: none;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    z-index: 998;
    opacity: 0;
    transition: all 0.5s ease-in-out;
}
.overlay.active {
    display: block;
    opacity: 1;
}

#sidebar .sidebar-header {
    padding: 10px;
    background: #007bff;
    color: #ffffff;
    font-size: small;
}

#sidebar .sidebar-header .row{
    margin-left: -15px;
}
#sidebar .sidebar-header ul{
    list-style: none;
}
#sidebar .sidebar-header button{
    background-color: #007bff;
    color: #ffffff;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    outline: #ffffff;
    margin: 0 2px;
    padding: 5px 50px;
    border-color: #ffffff;
}
.list-unstyled{
    overflow-y: scroll; 
    height: 600px;
}

hr{
    border-top: 5px solid #D5DBDB;
}
